<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-图标1">图标</a></li><li><a href="#vue-doc-迷你型1">迷你型</a></li><li><a href="#vue-doc-卡片样式1">卡片样式</a></li><li><a href="#vue-doc-禁用1">禁用</a></li><li><a href="#vue-doc-可关闭1">可关闭</a></li><li><a href="#vue-doc-自定义标签页1">自定义标签页</a></li><li><a href="#vue-doc-附加内容1">附加内容</a></li><li><a href="#vue-doc-不使用动画1">不使用动画</a></li><li><a href="#vue-doc-其它样式1">其它样式</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-tabs标签页1" class="vue-doc-element">Tabs 标签页</h1>
<p class="vue-doc-element">选项卡切换组件，常用于平级区域大块内容的的收纳和展现。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demoicon></demoicon>
<demomini></demomini>
<democard></democard>
<demodisabled></demodisabled>
<democlosable></democlosable>
<democustom></democustom>
<demoextra></demoextra>
<demoanimate></demoanimate>
<demoother></demoother>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-tabsprops1_1" class="vue-doc-element">Tabs props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>当前激活 tab 面板的 name，可以使用 v-model 双向绑定数据</td>
<td>String</td>
<td>默认为第一项的 name</td>
</tr>
<tr>
<td>type</td>
<td>页签的基本样式，可选值为 line 和 card</td>
<td>String</td>
<td>line</td>
</tr>
<tr>
<td>size</td>
<td>尺寸，可选值为 default 和 small，仅在 type=&quot;line&quot; 时有效</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>closable</td>
<td>是否可以关闭页签，仅在 type=&quot;card&quot; 时有效</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>animated</td>
<td>是否使用 CSS3 动画</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>capture-focus</td>
<td>Tabs 内的表单类组件是否自动获得焦点</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-tabsevents1_2" class="vue-doc-element">Tabs events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-click</td>
<td>tab 被点击时触发</td>
<td>name</td>
</tr>
<tr>
<td>on-tab-remove</td>
<td>tab 被关闭时触发</td>
<td>name</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-tabsslot1_3" class="vue-doc-element">Tabs slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>extra</td>
<td>附加内容</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-tabpaneprops1_4" class="vue-doc-element">TabPane props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用于标识当前面板，对应 value，默认为其索引值</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>选项卡头显示文字，支持 Render 函数。 学习 Render 函数的内容</td>
<td>String  Function</td>
<td>空</td>
</tr>
<tr>
<td>icon</td>
<td>选项卡图标</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用该选项卡</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>closable</td>
<td>是否可以关闭页签，仅在 type=&quot;card&quot; 时有效</td>
<td>Boolean</td>
<td>null</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th>Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demoicon from './demo/icon.md';
import demomini from './demo/mini.md';
import democard from './demo/card.md';
import demodisabled from './demo/disabled.md';
import democlosable from './demo/closable.md';
import democustom from './demo/custom.md';
import demoextra from './demo/extra.md';
import demoanimate from './demo/animate.md';
import demoother from './demo/other.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demoicon,demomini,democard,demodisabled,democlosable,democustom,demoextra,demoanimate,demoother
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


